<template>
  <div class="head">
    <van-nav-bar left-text="" :class="[{'active': headerActive},{'back-hader':type === 'back'}]" left-arrow>
      <template #title>
        {{title}}
        <template v-if="isIdo">
          <van-popover v-model:show="showPopoverIdo" placement="bottom" :actions="actionsIdo" @select="changeIdo">
            <template #reference>
              <div class="flex-center" style="padding-right:10px;">
                <div style="margin-right:10px;">{{idoFilter(idoType)}}</div>
                <img style="width:6px;height:auto;transform: rotate(90deg);" src="@/assets/images/arr-r.png" alt="">
              </div>
            </template>
          </van-popover>
        </template>
      </template>
      <template #left>
        <template v-if="type === 'back'">
          <img class="back" src="@/assets/images/back@2x.png" @click="$goBack" alt="">
        </template>
        <template v-else>
          <span class="header-title">
            <slot name="leftTitle"></slot>
          </span>
        </template>
      </template>
      <template #right>
        <slot name="rightTitle">
          <div v-if="address" class="address-btn address-copy" :data-clipboard-text="address">
            {{address.substring(0,4)}}...{{address.substring(address.length - 4, address.length)}}
          </div>
          <div v-else class="address-btn" @click="initWallet">
            {{t('index.鏈接錢包')}}
          </div>
        </slot>
        <template v-if="type !== 'back'">
          <van-popover v-model:show="showPopover" placement="bottom-end" :actions="actions" @select="changeLanuage">
            <template #reference>
              <div class="flex-center">
                <!-- {{localFilter(local)}}
              <img class="arrDown" :class="{'arrUp': showPopover}" src="@/assets/images/arr-b.png"> -->
                <img class="lang" src="@/assets/images/lang@2x.png" alt="">
              </div>
            </template>
          </van-popover>
          <img class="header-icon" src="@/assets/images/more.png" @click="onMenu" alt="" />
        </template>
      </template>

    </van-nav-bar>
    <div class="menu-Block" v-show="open" @click.self="closeMenu()">
      <div class="menu-leftBlock">
        <div class="menu-top">
          <div class="menu-top-left">
            <img class="logo-title" src="@/assets/images/logo.png" @error="$setErrorImg" alt="">
            BACK
          </div>
          <img class="header-icon" @click="closeMenu()" src="@/assets/images/close.png" alt="">
        </div>
        <div class="menus">
          <div class="menu-item flex-middle" @click="$router.push({name:'Index'})">
            <div class="left flex-item flex-middle">
              <img class="menu-img" src="@/assets/images/nav/1.png" />
              <div class="menu-tit flex-item flex-between">
                {{ t("index.首頁") }}
              </div>
            </div>
            <img class="arr" src="@/assets/images/arr-r.png" alt="">
          </div>
          <div class="menu-item flex-middle" @click="$router.push({name:'Idoa'})">
            <div class="left flex-item flex-middle">
              <img class="menu-img" src="@/assets/images/nav/2.png" />
              <div class="menu-tit flex-item flex-between">
                IDO
              </div>
            </div>
            <img class="arr" src="@/assets/images/arr-r.png" alt="">
          </div>
          <a class="menu-item flex-middle" href="https://aboutusback.github.io">
            <div class="left flex-item flex-middle">
              <img class="menu-img" src="@/assets/images/nav/3.png" />
              <div class="menu-tit flex-item flex-between">
                {{t('index.足球世界杯')}}
              </div>
            </div>
            <img class="arr" src="@/assets/images/arr-r.png" alt="">
          </a>
          <div class="menu-item flex-middle" @click="$isDevelopment">
            <div class="left flex-item flex-middle">
              <img class="menu-img" src="@/assets/images/nav/4.png" />
              <div class="menu-tit flex-item flex-between">
                DAO
              </div>
            </div>
            <img class="arr" src="@/assets/images/arr-r.png" alt="">
          </div>
          <div class="menu-item flex-middle" @click="$router.push({name:'Share'})">
            <div class="left flex-item flex-middle">
              <img class="menu-img" src="@/assets/images/nav/5.png" />
              <div class="menu-tit flex-item flex-between">
                {{t('index.邀請好友')}}
              </div>
            </div>
            <img class="arr" src="@/assets/images/arr-r.png" alt="">
          </div>
          <a href="https://aboutusback.github.io/threekingdoms" class="menu-item flex-middle">
            <div class="left flex-item flex-middle">
              <img class="menu-img" src="@/assets/images/nav/6.png" />
              <div class="menu-tit flex-item flex-between">
                {{t('index.三國演義')}}
              </div>
            </div>
            <img class="arr" src="@/assets/images/arr-r.png" alt="">
          </a>
          <a href="https://aboutusback.github.io/#/liquidity/mining" class="menu-item flex-middle">
            <div class="left flex-item flex-middle">
              <img class="menu-img" src="@/assets/images/nav/7.png" />
              <div class="menu-tit flex-item flex-between">
                {{t('index.流動性挖礦')}}
              </div>
            </div>
            <img class="arr" src="@/assets/images/arr-r.png" alt="">
          </a>
          <div class="menu-item flex-middle" @click="$router.push({name:'CommunityAwards'})">
            <div class="left flex-item flex-middle">
              <img class="menu-img" src="@/assets/images/nav/8.png" />
              <div class="menu-tit flex-item flex-between">
                {{t('index.社區獎勵')}}
              </div>
            </div>
            <img class="arr" src="@/assets/images/arr-r.png" alt="">
          </div>
        </div>
      </div>
    </div>
    <MyDialog v-model:isShow="isDialogPrompt" :title="t('index.綁定推薦節點地址')" :isShade="isShade">
      <template #content>
        <div class="input-wrap">
          <input type="text" v-model.trim="intro" :placeholder="t('index.請填寫邀請人地址')">
        </div>
      </template>
      <template #footer>
        <div class="btn-wrap dialog-btn" style="margin-top:0;">
          <van-button class="btn4" type="primary" :loading="bindBtn.loading" :disabled="bindBtn.loading || bindBtn.disabled" @click="bindIntroHandle" :loading-text="t('index.確定')+'...'">{{ t("index.確定") }}</van-button>
        </div>
      </template>
    </MyDialog>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch, getCurrentInstance, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { getLang, setLang } from "@/utils/i18n";
import { intro } from "@/storage"
import MyDialog from "@/components/MyDialog";
import web3Data from "@/utils/initWeb3";
import { useRoute } from 'vue-router'
import Clipboard from "clipboard";
import {
  Toast
} from "vant";
import router from "../router";
Toast.allowMultiple();
export default {
  components: {
    MyDialog
  },
  props: {
    type: {
      type: String,
      default: ""
    },
    title: {
      type: String,
      default: ""
    },
    isIdo: {
      type: Boolean,
      default: false
    }
  },
  setup (props) {
    const { proxy } = getCurrentInstance();
    const store = useStore();
    const { t } = useI18n();
    const route = useRoute()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      type: props.type,
      title: props.title,
      showPopover: false,
      showPopoverIdo: false,
      headerActive: false,
      local: getLang() || "zh-TW",
      intro: intro.get() || '',
      actions: [
        {
          text: 'English',
          value: 'en'
        },
        {
          text: '中文繁體',
          value: 'zh-TW'
        }
        // {
        //   text: '日本語',
        //   value: 'ja'
        // },
        // {
        //   text: '한국어',
        //   value: 'ko'
        // }
      ],
      actionsIdo: [
        {
          text: t('index.天使輪'),
          value: 'Ido0'
        },
        {
          text: t('index.{key}輪', { key: 'A' }),
          value: 'Idoa'
        }
      ],
      idoType: '',
      open: false,
      isDialogPrompt: false,
      isShade: false,
      bindBtn: {
        loading: false,
        disabled: false
      },
      address0: '0x0000000000000000000000000000000000000000',
      initWallet: () => {
        web3Data.initWeb3()
      },
      initContract: () => {
        data.idoType = route.name
        data.initData();
      },
      initData: () => {
      },
      pdfReportViewer: () => {
        let url = 'demo.pdf'
        if (data.local === 'en') {
          return url = 'demo.pdf'
        }
        window.open(`${window.location.origin}${window.location.pathname}pdf/web/viewer.html?file=${url}`)
      },
      onMenu: () => {
        data.open = !data.open;
      },
      closeMenu: () => {
        data.open = false;
      },
      changeLanuage: (val) => {
        setLang(val.value)
        data.local = val.value
        window.location.reload()
      },
      changeIdo: (val) => {
        data.idoType = val.value
        router.replace({ name: val.value })
      },
      localFilter (value) {
        let list = data.actions
        let res = ''
        for (let k = 0; k < list.length; k++) {
          if (list[k].value === value) {
            res = list[k].text
            break
          }
        }
        return res
      },
      idoFilter (value) {
        if (value === 'Ido0') {
          return t('index.天使輪')
        }
        if (value === 'Idoa') {
          return t('index.{key}輪', { key: 'A' })
        }
        return ''
      },
      getRootAddress: async () => {
        try {
          let res = await data.handleContract.methods.rootAddress().call({
            from: data.address
          });
          console.log(res, 'rootAddress')
          if (!res) {
            return Toast(t('index.頂點未設置'))
          }
          if (res !== data.address) {
            data.getIntroHandle()
          } else {
            console.log('頂點賬戶不展示綁定彈窗')
          }
        } catch (error) {
          proxy.$errorformat(error)
        }
      },
      getIntroHandle: async () => {
        const Toast1 = Toast.loading({
          duration: 0,
        });
        try {
          let res = await data.handleContract.methods.isBindIntro(data.address).call({
            from: data.address
          });
          console.log(res, 'isBindIntro')
          if (!res) {
            // 沒有推薦人
            data.isDialogPrompt = true
          }
          Toast1.clear()
        } catch (error) {
          Toast1.clear()
          proxy.$errorformat(error)
        }
      },
      bindIntroHandle: async () => {
        data.bindBtn.loading = true
        console.log(data.intro, 'bindIntroHandle')
        try {
          if (!web3Data.web3.utils.isAddress(data.intro)) {
            Toast(t("index.邀請人地址无效"));
            data.bindBtn.loading = false
            return false
          }
          await data.handleContract.methods.bindIntro(data.intro).estimateGas({
            from: data.address
          });
          let res = await data.handleContract.methods.bindIntro(data.intro).send({
            from: data.address
          });
          console.log(res, 'bindIntro')
          Toast(t("index.Success"));
          data.bindBtn.loading = false
          data.isShade = true
          data.isDialogPrompt = false
        } catch (error) {
          data.bindBtn.loading = false
          proxy.$errorformat(error)
        }
      },
    })
    const debounce = (fn, interval) => {
      var timer;
      var gapTime = interval || 150;
      return function () {
        clearTimeout(timer);
        var context = this;
        var args = arguments;
        timer = setTimeout(function () {
          fn.call(context, args);
        }, gapTime);
      };
    }
    document.addEventListener('scroll', debounce(() => {
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      data.headerActive = scrollTop > 30
    }))
    onMounted(() => {
      var clipboard = new Clipboard(".address-copy");
      clipboard.on("success", function (e) {
        Toast(t("share.複製成功"));
        e.clearSelection();
      });
      clipboard.on("error", function () {
        Toast(t("share.複製失敗，請手動長按複製"));
      });
    });
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>
<style lang="less" scoped>
.lang {
  width: 24px;
  height: 24px;
  display: block;
  margin-left: 10px;
}
.head {
  color: #b1beea;
  font-size: 14px;
  font-family: PingFang SC;
  font-weight: 500;
  line-height: 35px;
}
.back {
  width: 9px !important;
  height: 16px;
}
.arrDown {
  width: 11px !important;
  height: 11px;
  margin-left: 8px;
}
.arrUp {
  transform: rotateX(180deg);
}
.header-icon {
  width: 24px;
  height: 24px;
  display: block;
  margin-left: 10px;
}
.address-btn {
  height: 24px;
  background: #1a202e;
  border-radius: 12px;
  color: #fff;
  padding: 0 15px;
  line-height: 24px;
}
.menu-Block {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 500;
  .menu-leftBlock {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    // background: url(~@/assets/images/menu-bg.webp) no-repeat;
    background: #1a202e;
    left: 0%;
    .menus {
      height: calc(100vh - 63px);
      overflow-y: auto;
      padding-bottom: 50px;
    }
    .menu-top {
      margin: 10px 0 32px;
      display: flex;
      padding: 0 22px 0 16px;
      align-items: center;
      justify-content: space-between;
      .header-icon {
        width: 13px;
        height: 13px;
      }
      .logo-title {
        height: 25px;
        width: auto;
        margin-right: 7px;
        border-radius: 50%;
      }
      .menu-top-left {
        display: flex;
        align-items: center;
        font-size: 22px;
        font-family: Hiragino Sans GB;
        font-weight: normal;
        color: #ffffff;
        line-height: 1;
      }
    }
    .menu-item {
      padding: 11px 21px 11px 16px;
      line-height: 1.3;
      .menu-img {
        width: 20px;
        height: 20px;
        display: block;
      }
      .menu-tit {
        font-size: 14px;
        font-family: PingFang SC;
        font-weight: bold;
        color: #ffffff;
        margin-left: 11px;
        span {
          font-size: 16px;
          color: #ff9600;
        }
      }
      .arr {
        width: auto;
        height: 12px;
        display: block;
      }
    }
  }
}
</style>
<style lang="less">
#app {
  .header-title {
    display: flex;
    align-items: center;
    img {
      display: block;
      width: 25px;
      height: 25px;
      margin-right: 7px;
      border-radius: 50%;
    }
    font-size: 22px;
    font-family: Hiragino Sans GB;
    font-weight: normal;
    color: #ffffff;
    line-height: 1;
  }
  .van-nav-bar {
    &.active {
      transition: all 0.3s;
      background: #1a202e !important;
    }
    &.back-hader {
      transition: all 0.3s;
      background: transparent;
      box-shadow: initial;
    }
  }
}
</style>
