<template>
  <div class="my-dialog" v-if="isShow">
    <div class="shade" @click="closeHandle"></div>
    <div class="dialog-wrap">
      <div class="section">
        <div class="dialog anim-scale">
          <slot name="header">
            <div class="header">
              <img class="dialog-close" @click="$emit('update:isShow',false)" src="@/assets/images/close@2x.png" alt="">
              <div v-if="title" class="title">{{title}}</div>
            </div>
          </slot>
          <div class="content">
            <slot name="content">
              <div v-if="content">{{content}}</div>
            </slot>
          </div>
          <div class="footer">
            <slot name="footer">

            </slot>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs, watch } from "vue";
import { useI18n } from "vue-i18n";
export default {
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    content: {
      type: String,
      default: ''
    },
    isShade: {
      type: Boolean,
      default: false
    },
  },
  components: {
  },
  setup (props, { emit }) {
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      closeHandle: () => {
        if (props.isShade) {
          emit('update:isShow', false)
        }
      }
    })
    watch(
      () => props.isShow,
      (newVal) => {
        if (newVal) {
          document.querySelector('body').style.overflow = 'hidden'
        } else {
          document.querySelector('body').removeAttribute('style')
        }
      }
    )
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.my-dialog {
  position: relative;
  z-index: 2000;
  .shade {
    background-color: rgba(0, 0, 0, 0.5);
    pointer-events: auto;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  .dialog-wrap {
    display: table;
    font-family: Helvetica, arial, sans-serif;
    pointer-events: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    .section {
      display: table-cell;
      vertical-align: middle;
      text-align: center;
    }
    .dialog {
      line-height: 1.3;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      background: #1a202e;
      border-radius: 8px;
      width: 84% !important;
      border-radius: 0.5 rem;
      border: none;
      color: #fff;
      max-width: 640px;
      position: relative;
      display: inline-block;
      text-align: left;
      font-size: 14px;
      border-radius: 5px;
      pointer-events: auto;
      -webkit-overflow-scrolling: touch;
      -webkit-animation-fill-mode: both;
      animation-fill-mode: both;
      -webkit-animation-duration: 0.2s;
      animation-duration: 0.2s;
      .header {
        position: relative;
        .dialog-close {
          right: 7px;
          top: 15px;
          position: absolute;
          box-sizing: content-box;
          width: 13px;
          height: 13px;
          padding: 8px;
        }
      }
      .title {
        text-align: center;
        padding-top: 17px;
        font-size: 18px;
        font-family: PingFang SC;
        font-weight: bold;
        color: #ffffff;
      }
      .content {
        padding: 30px 33px 0;
        text-align: center;
        font-size: 15px;
        color: #fff;
        min-height: 119px;
        word-break: break-all;
      }
    }
  }
  @-webkit-keyframes anim-scale {
    0% {
      opacity: 0;
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }

    100% {
      opacity: 1;
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  }

  @keyframes anim-scale {
    0% {
      opacity: 0;
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }

    100% {
      opacity: 1;
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  }

  .anim-scale {
    animation-name: anim-scale;
    -webkit-animation-name: anim-scale;
  }
}
</style>
<style lang="less">
.my-dialog {
  .li {
    display: flex;
    align-items: center;
    &:not(:last-child) {
      margin-bottom: 13px;
    }
    .key {
      flex-shrink: 0;
    }
    .value {
    }
  }
  .input-wrap {
    display: flex;
    align-items: center;
    .input-label {
      font-size: 15px;
      font-weight: 600;
      color: #fff;
      .btn {
        min-width: 50px !important;
        font-size: 12px !important;
      }
    }
    input {
      height: 34px;
      background: rgba(26, 32, 46, 0.8);
      border-radius: 8px;
      font-size: 13px;
      font-family: PingFang SC;
      font-weight: 500;
      color: #ffffff;
      padding: 0 13px;
      width: 100%;
    }
  }
  .input-info {
    width: 100%;
    text-align: right;
    margin-top: 15px;
  }
  .select-wrap {
    select {
      border: 0;
      font-size: 15px;
      color: #fff;
      text-align: right;
      &:focus-visible {
        outline: 0;
      }
    }
  }
  .key {
    font-size: 15px;
    color: #fff;
  }
  .value {
    font-size: 15px;
    color: #fff;
    word-break: break-all;
    text-align: left;
    font-weight: 600;
  }
  .dialog-des {
    font-size: 13px;
    text-align: left;
  }
  .status {
    font-size: 16px;
    color: #fff;
    padding-top: 4px;
  }
  .info {
    font-size: 16px;
    color: #fff;
    padding-top: 4px;
  }
  .dialog-btn {
    margin-top: 0;
    padding: 0px 16px 25px;
  }
}
</style>
